.container__divisas {
    width: 100%;
    min-height: 10rem;
    background: #fafafa;
    position: relative;
}

.container_divisas {
    width: 100%;
    max-width: 1200px;
    margin: auto;
    padding: .4rem 1rem 3rem 1rem;
}

.container_divisas .panel_change {
    width: 60%;
    height: 100%;
    min-width: 300px;
    margin: 0 auto;
    margin-top: 0.5rem;
    text-align: center;
}

.container_divisas .panel_change h2 {
    color: var(--text-azul);
    margin-bottom: .4rem;
}

.change {
    width: 100%;
    display: grid;
    background-color: var(--blanco);
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    border-radius: .5rem;
}




/*================================== Splash ==================================*/
.splash {
    position: relative;
    margin: 2rem auto;
    max-width: 600px;
    width: 93%;
    min-width: 300px;
    padding: 3rem;
    text-align: center;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    row-gap: 5px;

    border: 2px solid var(--verde);
    border-radius: 1rem;

    color: var(--text-azul);
    background-color: rgba(137, 236, 57, 1);
}

.button_splash a {
    color: white;
}

.splash>.ads {
    position: absolute;
    top: -50px;
    left: 70px;
    width: 90px;
    height: 90px;
    padding: 10px;
    transform: rotate(-25deg);
    /* background-color: #fafafa; */
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 1px 2px 0px;

}

.splash>.ads img {
    width: 90%;
}


.splash>h2 {
    font-size: 1.5rem;
}

.splash>span {
    font-size: 1rem;
    font-weight: 400;
    color: #2E2E2E;
}

.splash .button_splash {
    position: relative;
    padding: 1rem;
    border-radius: 4px;
    color: #fcfcfc;
    background: var(--text-azul);
    margin-top: 20px;
    font-weight: 600;
    font-size: 1.5rem;
}



/* ================== Tablero Divisas Change =============== */

.change_divisa {
    margin: 0 auto;
    padding: 2rem .2rem;
    width: 60%;
    min-width: 300px;
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 0 5px;
}


.change_divisa .line::before {
    content: '';
    border: 2px solid var(--verde);
    border-radius: 2px;
    margin-right: 5px;
}

.change_divisa .line::after {
    content: '';
    border: 2px solid var(--verde);
    border-radius: 2px;
    margin-left: 5px;
}

.change_divisa .line small {
    font-weight: 600;
    text-transform: uppercase;
    font-size: 12px;
}



/* ================= Divisas ===================*/

.change ul {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding: 1.4rem;
}

.change ul li small {
    font-size: .9rem;
    color: #272727;
}


.change ul li #divisa {
    width: 3rem;
    height: 3rem;
    border-radius: 3rem;
    margin: auto auto 10px auto;
    cursor: pointer;
    transition: all 100ms linear;

}

.compra1 h3 small,
.venta1 h3 small {
    font-size: 20px;
}


