/* =============== Calculator ========================= */

.calculator {
    padding: 10px;
    margin: 10px auto 20px auto;
}

.calculator button {
    padding: 10px 15px;
    font-weight: 500;
    border-radius: 10px;
    background-color: #91dc55;
    color: white;
    cursor: pointer;
}

.calculator button:hover {
    background-color: #89EC39;
}

/* =============== Moral calculator ========================= */
.container_calculator {
    position: relative;
    min-width: 300px;
    padding: 10px;
}

.title_calculator {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    margin-bottom: 20px;
}

.title_calculator h2 {
    font-size: 1.3rem;
}

.title_calculator .date {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: .9rem;
}

.container_converter {
    width: 100%;
    height: auto;
    padding: 20px 0;
    border-radius: 5px;

    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: auto auto;
}

.css-f4c2o3 {
    max-width: 500px;
    width: calc(96% - 10px);
}

/* Container Sed */
.converter_sed {
    width: 100%;
}

.converter_sed .type_operation {
    width: 100%;
    padding: 10px;

    /* grid */
    display: grid;
    grid-template-columns: 50% 50%;
    justify-content: center;
    align-items: center;
}

.converter_sed .type_operation div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column-reverse;
    gap: 10px;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type=number] {
    appearance: textfield;
}


.converter_sed .type_operation div label {
    font-weight: 600;
}

.converter_sed .type_operation div input[type=number] {
    text-align: center;
}

.converter_sale {
    width: 100%;
    margin: 30px auto auto auto;
    background-color: rgba(23, 211, 6, 0.02);
    border: 3px dashed #DCEAEB;
    border-radius: 13px;
    padding: 20px 0;
    overflow: hidden;
}

.converter_sale .container_value_operation {
    width: calc(100%);
    display: grid;
    grid-template-columns: 50% 50%;
    justify-content: center;
    align-items: center;
}


.converter_sale .container_value_operation div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.converter_sale .container_value_operation div select,
.converter_sale .container_value_operation div input[type=number] {
    height: 25px;
    cursor: pointer;
    margin-top: 3px;
    border-radius: 6px;
    text-align: center;
}



.converter_sale .container_value_operation div label {
    font-size: 11px;
    font-weight: 600;
}

.converter_sale .result_calculo {
    width: 50%;
    margin: 40px auto auto auto;

    display: grid;
    grid-template-rows: auto auto;
    align-items: center;
    justify-content: center;
}

.converter_sale .result_calculo div {
    text-align: center;
}

.converter_sale .result_calculo div.success {
    font-size: 18px;
    /* color: rgb(23, 211, 6); */
}

.converter_sale .result_calculo div.danger {
    font-size: 18px;
    /* color: rgb(211, 30, 6); */
}

.converter_sale .result_calculo div:nth-child(2) {
    font-size: 13px;
    font-weight: 600;
    color: black;
}

.container_solicitar-tasa {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container_solicitar-tasa>.description_swiper {
    width: max-content;
}

.container_solicitar-tasa>.description_swiper .solicitar {
    background-color: #91dc55;
    border: none;
}


.Close_modal {
    padding: 3px 4px;
    font-size: 25px;
    color: #AEAEAE;
    border: 1px solid #EEEEEE;
    border-radius: 7px;
    background-color: #FBFBFB;
    cursor: pointer;
    transition: ease .19s;
    font-weight: 600;
}

.Close_modal:hover {
    background-color: #F85151;
    color: white;
    border: 1px solid #DA3131;
}

/* =============== Video - Wrapper ========================= */

.container-wrapper {
    position: relative;
    width: 100%;
    background: linear-gradient(0deg, rgba(250, 250, 250, 1) 0%, rgba(241, 241, 241, 1) 100%);
    padding: 50px 0 20px 0;
}

.video-wrapper {
    position: relative;
    padding: 50px 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.video-wrapper iframe {
    max-width: 100%;
    max-height: 100%;
}

.container-title-wrapper {
    text-align: center;
    color: var(--text-azul);
}

.title {
    font-size: 24px;
    margin-bottom: 3px;
    padding: 0 10px;
}

.description {
    font-size: 18px;
}



/* =============== Funcionalidad ========================= */

.change ul li>.active_divisa#divisa {
    border: .3rem solid var(--verde);
}


@media only screen and (max-width:1065px) {}

@media only screen and (max-width:900px) {
    .container_divisas .panel_change {
        width: var(--container-width-md);
    }
}

@media only screen and (max-width:600px) {

    .container_calculator{
        padding: 0;
    }

    .converter_sale .container_value_operation{
        grid-template-columns: 35% auto;
    }

    .container_divisas .panel_change {
        width: 95%;
        height: 100%;
        min-width: 300px;
        margin: 0 auto;
        text-align: center;
    }

    .change_divisas {
        padding: 1rem 0rem 3rem 0rem;
        display: grid;
        grid-template-columns: 30% 30% 30%;
    }

    .line {
        min-width: 100px;
    }

    .change ul {
        padding: .5rem;
    }

    .change ul li small {
        display: none;
    }

    .dolar2 {
        display: none;
    }
}